<template>
    <v-container>
        <v-container fluid class="text-right">
            <v-row>
                <v-col cols="4">
                    <v-autocomplete v-model="manual_review.analystName" :items="analysts" label="Analista Principal"
                        :rules="notEmptyRule"></v-autocomplete>
                </v-col>
                <v-col cols="4">
                    <v-autocomplete v-model="manual_review.analystName2" :items="analysts"
                        label="Analista Secundário"></v-autocomplete>
                </v-col>
                <v-col cols="4">
                    <v-select v-model="manual_review.period" :items="periodos" label="Período"
                        :rules="notEmptyRule"></v-select>
                </v-col>
            </v-row>

            <v-row v-if="manual_review.analystName2 == ''">
                <v-col cols="12" md="6">
                    <v-textarea filled name="input-7-4" label="Comentário Geral"
                        v-model="manual_review.comment"></v-textarea>
                </v-col>
            </v-row>
            <v-row v-else>
                <v-col cols="6" md="6">
                    <v-textarea filled name="input-7-4" label="Comentário Analista Principal"
                        v-model="manual_review.comment"></v-textarea>
                </v-col>
            </v-row>
            <v-btn class="ma-2 ml-1" color="primary" @click="send"
                :disabled="manual_review.comment == '' || manual_review.period == null">
                Adicionar Comentário
            </v-btn>
        </v-container>

        <v-dialog v-model="dialog" hide-overlay persistent width="300">
            <v-card color="primary" dark>
                <v-card-text>
                    Processando...
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>


    </v-container>
</template>

<script>
/* global tableau */
/* eslint-disable */

import axios from "axios";
import config from "../utils/config";
import Swal from "sweetalert2";

export default {
    name: "CommentComponent",
    data() {
        return {
            periodos: [],
            firstComment: true,
            analysts: [],
            manual_review: {
                comment: "",
                comment2: "",
                created_at: null,
                sise_key: null,
                user_name: null,
                analystName: "",
                analystName2: "",
                coverage_id: null,
                period: null,
                stage: null,
                porcVerification: null,
                region: null,
            },
            payloadData: {},
            tableauUser: null,
            dialog: false,
        };
    },
    methods: {
        send() {
            const payload = {
                ...this.payloadData,
                comment_data: {
                    analystName: this.manual_review.analystName,
                    analystName2: this.manual_review.analystName2 || null,
                    period: this.manual_review.period,
                    comment: this.manual_review.comment,
                },
                tableau_user: this.tableauUser,
            };

            this.dialog = true; // Exibir barra de progresso

            console.log("Dados enviados para a API:", payload);

            // Primeiro envio: comentário
            axios.post(`${config.AWS_URL}/create_comment_detectivos`, payload)
                .then((response) => {
                    console.log("Resposta da API (comentário):", response.data);

                    // Segundo envio: e-mail
                    return axios.post(`${config.AWS_URL}/send_email_detectivos`, payload);
                })
                .then((response) => {
                    console.log("Resposta da API (e-mail):", response.data);

                    // Fechar a modal com sinal de sucesso
                    this.dialog = false;
                    tableau.extensions.ui.closeDialog("success"); // Retorna "success" para o componente principal
                })
                .catch((error) => {
                    console.error("Erro ao processar os dados:", error);

                    // Exibir erro e ocultar barra de progresso
                    this.dialog = false;
                    Swal.fire({
                        icon: "error",
                        title: "Erro",
                        text: "Erro ao salvar ou enviar dados. Verifique o servidor.",
                    });
                });
        },
        async getTableauUser() {
            try {
                console.log("Inicializando contexto do Tableau...");
                const context = await tableau.extensions.environment.context;

                console.log("Contexto do Tableau Extensions:", context);
                this.tableauUser = context.userName || "Usuário não definido (desktop ou não autenticado)";
                console.log("Usuário do Tableau:", this.tableauUser);
            } catch (error) {
                console.error("Erro ao obter o contexto do Tableau Extensions:", error);
                this.tableauUser = "Erro ao obter usuário";
            }
        },
        fetchAnalysts() {
            const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(
                (w) => w.name === "Supp_analista_ajuste"
            );
            worksheet.getSummaryDataAsync().then((dataTable) => {
                this.analysts = dataTable.data
                    .map((row) => row[0]._value === "%null%" ? "" : row[0]._value)
                    .filter((value) => value)
                    .sort();
                console.log("Analistas carregados:", this.analysts);
            });
        },
        generatePeriods() {
            const today = new Date();
            for (let i = 0; i < 3; i++) {
                const month = new Date(today.getFullYear(), today.getMonth() - i, 1);
                const formatted = `${month.getFullYear()}-${String(month.getMonth() + 1).padStart(2, "0")}`;
                this.periodos.push(formatted);
            }
            console.log("Períodos gerados:", this.periodos);
        },
    },
    async mounted() {
        this.generatePeriods();

        this.manual_review.period = this.periodos[0];
        console.log("Período pré-selecionado:", this.manual_review.period);

        tableau.extensions.initializeDialogAsync().then((openPayload) => {
            console.log("Dados recebidos do componente anterior (payload):", openPayload);

            try {
                const data = JSON.parse(openPayload);
                this.payloadData = data;

                this.manual_review.analystName = data.form_data.analyst_name_adjustment === "%null%" ? "" : data.form_data.analyst_name_adjustment;

                this.manual_review.region = data.form_data.grupo_lob || "";
                this.manual_review.stage = data.form_data.isR2 ? "2" : "1";
                console.log("Dados inicializados em manual_review:", this.manual_review);
            } catch (error) {
                console.error("Erro ao processar os dados recebidos:", error);
            }
            this.getTableauUser()
                .then(() => {
                    this.fetchAnalysts();
                })
                .catch((error) => {
                    console.error("Erro ao obter usuário do Tableau:", error);
                });
            this.fetchAnalysts();
        });
    },
};
</script>

