<template>
    <v-container>
        <v-toolbar color="primary" cards dark flat>
            <v-card-title class="col-6 title font-weight-regular">
                Revisão Detectivos
            </v-card-title>
            <v-select class="pt-5" v-model="manual_review.period" :items="periodos" label="Período"
                :rules="notEmptyRule"></v-select>
        </v-toolbar>

        <v-form ref="form" class="pa-4 pt-6">
            <v-row>
                <v-col cols="6">
                    <v-subheader>
                        <p class="font-weight-bold">Resultado</p>
                    </v-subheader>
                </v-col>
                <v-col cols="6">
                    <v-subheader>
                        <v-autocomplete 
                            v-model="resultado_detectivos" 
                            :items="resultados" 
                            label="Resultado"
                        ></v-autocomplete>
                    </v-subheader>
                </v-col>
            </v-row>

            <v-row>
                <v-col cols="6">
                    <v-subheader>
                        <p class="font-weight-bold">Observação</p>
                    </v-subheader>
                </v-col>
                <v-col cols="6">
                    <v-textarea 
                        filled 
                        name="input-7-4" 
                        label="Comentário" 
                        v-model="manual_review.comment"
                        :rules="commentRules"
                    ></v-textarea>
                </v-col>
            </v-row>
        </v-form>

        <v-divider></v-divider>

        <v-card-actions>
            <v-btn text @click="closeDialog"> Cancelar </v-btn>
            <v-spacer></v-spacer>
            <v-btn 
                class="white--text" 
                color="primary" 
                @click="saveDialog" 
                :disabled="manual_review.period == null"
            >
                Salvar
            </v-btn>
        </v-card-actions>

        <v-dialog v-model="dialog" hide-overlay persistent width="300">
            <v-card color="primary" dark>
                <v-card-text>
                    Processando...
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-snackbar v-model="error_dialog">
            {{ error_text }}
            <template v-slot:action="{ attrs }">
                <v-btn color="pink" text v-bind="attrs" @click="error_dialog = false">
                    Fechar
                </v-btn>
            </template>
        </v-snackbar>
    </v-container>
</template>

<script>
/* global tableau */
/* eslint-disable */
import config from "../utils/config";

export default {
    data() {
        return {
            periodos: [],
            resultados: [
                "APONTAMENTO INDEVIDO",
                "DOCUMENTO SUPORTE OK",
                "IRREGULAR",
                "REGULAR"
            ],
            justificativas: [],
            resultado_detectivos: null,
            manual_review: {
                period: null,
                comment: ""
            },
            dialog: false,
            error_dialog: false,
            error_text: "",
            commentRules: [(v) => !!v || 'Comentário é obrigatório'],
            receivedData: {} // Para armazenar os dados recebidos
        };
    },
    methods: {
        closeDialog() {
            tableau.extensions.ui.closeDialog("Cancelado");
        },
        cleanObjectValues(obj) {
            // Limpa recursivamente os valores inválidos no objeto
            if (typeof obj === "object" && obj !== null) {
                for (const key in obj) {
                    if (obj.hasOwnProperty(key)) {
                        if (typeof obj[key] === "object") {
                            this.cleanObjectValues(obj[key]);
                        } else {
                            obj[key] = obj[key] === "%null%" || obj[key] === "NA" ? "" : obj[key];
                        }
                    }
                }
            }
        },
        async saveDialog() {
            if (this.resultado_detectivos === "IRREGULAR" || this.resultado_detectivos === "APONTAMENTO INDEVIDO") {
                if (!this.manual_review.comment) {
                    this.error_text = "Comentário é obrigatório para este resultado";
                    this.error_dialog = true;
                    return;
                }
            }

            // Combine os dados recebidos e os dados do formulário no formato esperado
            const payload = {
                ...this.receivedData,
                competence: this.manual_review.period, // Inclui o período como competence
                result: this.resultado_detectivos,
                comment: this.manual_review.comment
            };

            // Limpar valores inválidos no payload
            this.cleanObjectValues(payload);

            console.log("Dados a serem enviados:", payload);

            this.dialog = true;

            try {
                const response = await fetch(`${config.AWS_URL}/manual_review_detectivos`, {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify(payload)
                });

                if (!response.ok) {
                    throw new Error(`Erro na requisição: ${response.statusText}`);
                }

                const result = await response.json();
                console.log("Resposta do servidor:", result);

                this.dialog = false;
                tableau.extensions.ui.closeDialog("Salvo com sucesso");
            } catch (error) {
                console.error("Erro ao salvar os dados:", error);
                this.error_text = "Erro ao salvar os dados. Verifique o servidor.";
                this.error_dialog = true;
                this.dialog = false;
            }
        }
    },
    mounted() {
        tableau.extensions.initializeDialogAsync().then((payload) => {
            // Payload contém os dados enviados pelo outro componente
            this.receivedData = JSON.parse(payload);
            console.log("Dados recebidos do outro componente:", this.receivedData);
        }).catch((error) => {
            console.error("Erro ao inicializar a extensão do Tableau:", error);
            this.error_text = "Erro ao inicializar a extensão. Verifique o ambiente.";
            this.error_dialog = true;
        });

        const today = new Date();
        for (let i = 0; i < 3; i++) {
            const month = new Date(today.getFullYear(), today.getMonth() - i, 1);
            const formatted = `${month.getFullYear()}-${String(month.getMonth() + 1).padStart(2, '0')}`;
            this.periodos.push(formatted);
        }
        this.manual_review.period = this.periodos[0];
    }
};
</script>

<style scoped></style>
