import Vue from "vue";
import Router from "vue-router";
import Main from "@/components/MainComponent";
import ReviewDetectivos from "@/components/ReviewDetectivos";
import Comment from "@/components/Comment.vue";

Vue.use(Router);

export default new Router({
    routes: [{
        path: "/",
        name: "Main",
        component: Main
    },
    {
        path: "/review_detectivos",
        name: "ReviewDetectivos",
        component: ReviewDetectivos
    },
    {
        path: "/comment",
        name: "Comment",
        component: Comment
    }]
});