<template>
  <div class="margens">
    <div class="row mt-1 ml-2" style="flex-wrap: nowrap; width: 100%;">
      <v-btn v-if="!isStarted" class="ma-2 ml-1 col-3" color="normal" @click="startReview" :disabled="isStarted">
        <v-icon dark left> mdi-arrow-right-drop-circle-outline </v-icon>
        Iniciar Revisão
      </v-btn>

      <v-btn v-if="isStarted && !isFinished" class="ma-2 ml-1 col-3" color="normal" @click="finishReview">
        <v-icon dark left> mdi-check-circle-outline </v-icon>
        Finalizar
      </v-btn>

      <v-btn v-if="isFinished" class="ma-2 ml-1 col-3" color="normal" @click="reopenReview">
        <v-icon dark left> mdi-reload </v-icon>
        Reabrir
      </v-btn>
      <v-autocomplete class="col-2" v-model="grupo_lob" :items="lobGroups" label="Grupo Lob"
        :disabled="!isStarted || isFinished"></v-autocomplete>
      <v-autocomplete class="col-2" v-model="area_type" :items="areas" label="Área Tipo"
        :disabled="!isStarted || isFinished"></v-autocomplete>
      <v-switch class="col-2 ma-2" v-model="isR2" inset :label="isR2 ? 'R2' : 'R1'"
        :disabled="!isStarted || isFinished"></v-switch>
    </div>

    <div class="row mt-1 ml-2" style="align-items: center;">
      <v-btn class="ma-2 ml-1 col-3 small-text" color="normal" @click="addComment" :disabled="!isStarted || isFinished">
        <v-icon dark left>mdi-file-plus</v-icon>
        Adicionar Comentário
      </v-btn>
      <v-autocomplete class="col-2" v-model="analyst_name_adjustment" :items="analysts_adjustment"
        label="Analista Ajuste" :disabled="!isStarted || isFinished"></v-autocomplete>
      <v-autocomplete class="col-2" v-model="analyst_name_request" :items="analysts_request" label="Analista Requisição"
        :disabled="!isStarted || isFinished"></v-autocomplete>
      <v-autocomplete class="col-2" v-model="analyst_name_approval" :items="analysts_approval"
        label="Analista Aprovação" :disabled="!isStarted || isFinished"></v-autocomplete>
    </div>
  </div>
</template>

<script>
/* global tableau */
/* eslint-disable */
export default {
  data() {
    return {
      analyst_name_adjustment: null,
      analyst_name_request: null,
      analyst_name_approval: null,
      grupo_lob: null,
      area_type: null,
      isR2: false,
      isStarted: false,
      isFinished: false,
      analysts: [],
      lobGroups: [],
      areas: [],
    };
  },
  methods: {
    startReview() {
      this.isStarted = true;
      this.isFinished = false;
      console.log("Revisão iniciada");
    },
    finishReview() {
      this.isFinished = true;
      console.log("Revisão finalizada");
    },
    reopenReview() {
      this.isStarted = true;
      this.isFinished = false;
      console.log("Revisão reaberta");
    },
    initializeDashboard() {
      if (!tableau.extensions.dashboardContent || !tableau.extensions.dashboardContent.dashboard) {
        console.error("Conteúdo do Dashboard não está disponível. Verifique a inicialização.");
        return;
      }

      console.log("Conteúdo do Dashboard disponível:", tableau.extensions.dashboardContent.dashboard);

      // Configurar dados e ouvintes
      this.fetchAnalystsData();
      this.fetchLobData();
      this.fetchAreaData();
      this.fetchSiseKey();
      this.addMarkSelectionEventHandler();
      this.fetchDetailsData();
      this.addWorksheetListeners();
    },

    // Atualizar todas as fontes de dados e planilhas
    updateAllData() {
      return new Promise((resolve, reject) => {
        if (!tableau.extensions.dashboardContent || !tableau.extensions.dashboardContent.dashboard) {
          console.error("Dashboard não está disponível. A API do Tableau pode não estar inicializada corretamente.");
          reject("Dashboard não inicializado.");
          return;
        }

        const dashboard = tableau.extensions.dashboardContent.dashboard;
        const promises = dashboard.worksheets.map((worksheet) =>
          worksheet.getDataSourcesAsync().then((dataSources) => {
            const refreshPromises = dataSources.map((dataSource) => {
              if (dataSource.refreshAsync) {
                console.log(`Atualizando fonte de dados: ${dataSource.name}`);
                return dataSource.refreshAsync();
              }
              console.warn(`A fonte de dados '${dataSource.name}' não suporta atualização.`);
              return Promise.resolve();
            });
            return Promise.all(refreshPromises);
          })
        );

        Promise.all(promises)
          .then(() => {
            console.log("Todas as fontes de dados foram atualizadas.");
            resolve();
          })
          .catch((error) => {
            console.error("Erro ao atualizar os dados do Tableau:", error);
            reject(error);
          });
      });
    },

    // Atualizar planilhas
    updateWorksheets() {
      const dashboard = tableau.extensions.dashboardContent.dashboard;
      dashboard.worksheets.forEach((worksheet) => {
        worksheet.getSummaryDataAsync()
          .then(() => {
            console.log(`Planilha '${worksheet.name}' atualizada.`);
          })
          .catch((error) => {
            console.error(`Erro ao atualizar a planilha '${worksheet.name}':`, error);
          });
      });
    },
    addComment() {
      const dataForModal = {
        sise_key: this.sise_key,
        form_data: {
          analyst_name_adjustment: this.analyst_name_adjustment || "",
          grupo_lob: this.grupo_lob || "",
          area_type: this.area_type || "",
          isR2: this.isR2 ? 2 : 1,
        },
      };

      console.log("Dados enviados para a modal:", dataForModal);

      const popupUrl = `${window.location.origin}/smartqa-extension-detectivos/#/comment`;

      tableau.extensions.ui
        .displayDialogAsync(popupUrl, JSON.stringify(dataForModal), {
          height: 400,
          width: 800,
        })
        .then((closePayload) => {
          console.log("Modal fechada com sucesso. Dados recebidos:", closePayload);

          if (closePayload === "success") {
            this.updateAllData()
              .then(() => {
                console.log("Fontes de dados e planilhas atualizadas com sucesso após a modal.");
              })
              .catch((error) => {
                console.error("Erro ao atualizar os dados do painel:", error);
              });
          } else {
            console.warn("Modal fechada sem sucesso ou sem necessidade de atualização.");
          }
        })
        .catch((error) => {
          if (error.errorCode === tableau.ErrorCodes.DialogClosedByUser) {
            console.log("Modal foi fechada pelo usuário.");
          } else {
            console.error("Erro ao abrir a modal:", error.message);
          }
        });
    },
    fetchSiseKey() {
      const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "DETALHE1"
      );

      if (worksheet) {
        worksheet.getSummaryDataAsync().then((dataTable) => {
          const siseKeyIndex = dataTable.columns.findIndex((col) => col.fieldName === "Nº Sise");
          if (siseKeyIndex !== -1 && dataTable.data.length > 0) {
            this.sise_key = dataTable.data[0][siseKeyIndex]?._value || null;
            console.log("Sise Key carregado:", this.sise_key);
          } else {
            console.warn("Sise Key não encontrado na planilha 'DETALHE1'.");
            this.sise_key = null;
          }
        }).catch((error) => {
          console.error("Erro ao carregar Sise Key:", error);
        });
      } else {
        console.error("Planilha 'DETALHE1' não encontrada.");
      }
    },
    openReviewDetectivos(dataTable) {
      const popupUrl = `${window.location.origin}/smartqa-extension-detectivos/#/review_detectivos`;

      tableau.extensions.ui
        .displayDialogAsync(popupUrl, JSON.stringify(dataTable), {
          height: 600,
          width: 600,
        })
        .then((closePayload) => {
          console.log("Modal fechada com sucesso. Dados recebidos:", closePayload);

          if (closePayload === "Salvo com sucesso") {
            this.updateAllData()
              .then(() => {
                console.log("Fontes de dados e planilhas atualizadas com sucesso após a modal.");
              })
              .catch((error) => {
                console.error("Erro ao atualizar os dados do painel:", error);
              });
          } else {
            console.log("Modal fechada sem sucesso ou sem necessidade de atualização.");
          }
        })
        .catch((error) => {
          if (error.errorCode === tableau.ErrorCodes.DialogClosedByUser) {
            console.log("Modal foi fechada pelo usuário.");
          } else {
            console.error("Erro ao abrir a modal:", error.message);
          }
        });
    },
    addMarkSelectionEventHandler() {
      const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "REVISÃO"
      );

      if (worksheet) {
        worksheet.addEventListener(tableau.TableauEventType.MarkSelectionChanged, async (event) => {
          if (this.isFinished || !this.isStarted) {
            console.warn("Revisão finalizada. Modal bloqueada.");
            return;
          }

          try {
            const marks = await event.getMarksAsync();
            if (marks && marks.data && marks.data.length > 0) {
              const dataTable = marks.data[0];

              const validadorTaskValue = this.getFieldValue(dataTable, "Validador Task");

              const resultadoVerificacao = this.getFieldValue(dataTable, "Resultado Verificação") || "Indefinido";
              const controle = this.getFieldValue(dataTable, "Controle") || "Indefinido";
              const tipoControle = this.getFieldValue(dataTable, "Tipo Controle") || "Indefinido";

              let detailedData = {};
              try {
                detailedData = await this.fetchDetailsData();
                detailedData = this.cleanObjectValues(detailedData); 
              } catch (error) {
                console.error("Erro ao obter dados detalhados:", error);
              }

              // Dados do formulário
              const formData = {
                adjustment_analyst: this.analyst_name_adjustment || "",
                request_analyst: this.analyst_name_request || "",
                approval_analyst: this.analyst_name_approval || "",
                lob_group: this.grupo_lob || "",
                area_type: this.area_type || "",
                stage: this.isR2 ? 2 : 1
              };

              const modalData = {
                ...detailedData,
                ...formData,
                resultadoVerificacao,
                controle,
                tipoControle,
                validadorTask: validadorTaskValue,
              };

              console.log("Dados enviados para a modal:", modalData);

              this.openReviewDetectivos(modalData);
            } else {
              console.warn("Marcas selecionadas não contêm dados válidos.");
            }
          } catch (error) {
            console.error("Erro ao processar seleção de marca:", error);
          }
        });
      } else {
        console.error("Planilha 'REVISÃO' não encontrada.");
      }
    },
    getFieldValue(dataTable, fieldName) {
      const colIndex = dataTable.columns.findIndex(col => col.fieldName === fieldName);
      if (colIndex !== -1) {
        return dataTable.data[0][colIndex]._value;
      }
      return null;
    },
    fetchAnalystsData() {
      const worksheets = [
        { name: "Supp_analista_ajuste", target: "analysts_adjustment" },
        { name: "Supp_analista_requisicao", target: "analysts_request" },
        { name: "Supp_analista_aprovacao", target: "analysts_approval" },
      ];

      worksheets.forEach(({ name, target }) => {
        const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(w => w.name === name);
        if (!worksheet) {
          console.error(`Planilha '${name}' não encontrada.`);
          return;
        }

        worksheet.getSummaryDataAsync().then((dataTable) => {
          const analysts = [];
          dataTable.data.forEach((row) => {
            if (row[0] && row[0]._value) {
              let value = row[0]._value;
              if (value === "%null%") {
                value = "";
              }
              analysts.push(value);
            }
          });

          this[target] = [...new Set(analysts)].sort();
          console.log(`Dados carregados para '${target}':`, this[target]);
        }).catch((error) => {
          console.error(`Erro ao carregar dados da planilha '${name}':`, error);
        });
      });

      this.fetchPreSelectedData();
    },
    fetchPreSelectedData() {
      const mappings = [
        { worksheetName: "DETALHE2", fieldName: "Analista Ajuste", target: "analyst_name_adjustment" },
        { worksheetName: "DETALHE3", fieldName: "Analista Requisição", target: "analyst_name_request" },
        { worksheetName: "DETALHE4", fieldName: "Analista Aprovação", target: "analyst_name_approval" },
        { worksheetName: "DETALHE1", fieldName: "Grupo Lob", target: "grupo_lob" },
        { worksheetName: "DETALHE1", fieldName: "Área Tipo", target: "area_type" },
      ];

      mappings.forEach(({ worksheetName, fieldName, target }) => {
        const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(w => w.name === worksheetName);
        if (!worksheet) {
          console.error(`Planilha '${worksheetName}' não encontrada.`);
          return;
        }

        worksheet.getSummaryDataAsync().then((dataTable) => {
          const colIndex = dataTable.columns.findIndex(col => col.fieldName === fieldName);
          if (colIndex !== -1 && dataTable.data.length > 0) {
            const value = dataTable.data[0][colIndex]?._value || "null";
            this[target] = value;
            console.log(`Valor pre-selecionado para '${target}':`, this[target]);
          } else {
            console.warn(`Campo '${fieldName}' não encontrado em '${worksheetName}'.`);
            this[target] = "null";
          }
        }).catch((error) => {
          console.error(`Erro ao obter dados de '${worksheetName}':`, error);
        });
      });
    },
    fetchLobData() {
      const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "Supp_lob"
      );
      if (!worksheet) {
        console.log("Erro: Planilha 'Supp_lob' não encontrada.");
        return;
      }
      worksheet.getSummaryDataAsync().then((dataTable) => {
        dataTable.data.forEach((row) => {
          if (row[0] && row[0]._value) {
            this.lobGroups.push(row[0]._value);
          }
        });
        this.lobGroups = [...new Set(this.lobGroups)].sort();
      }).catch((error) => {
        console.error("Erro ao obter dados da planilha 'Supp_lob':", error);
      });
    },
    fetchAreaData() {
      const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "Supp_area"
      );
      if (!worksheet) {
        console.log("Erro: Planilha 'Supp_area' não encontrada.");
        return;
      }
      worksheet.getSummaryDataAsync().then((dataTable) => {
        dataTable.data.forEach((row) => {
          if (row[0] && row[0]._value) {
            this.areas.push(row[0]._value);
          }
        });
        this.areas = [...new Set(this.areas)].sort();
      }).catch((error) => {
        console.error("Erro ao obter dados da planilha 'Supp_area':", error);
      });
    },
    fetchDetailsData() {
      const detailsWorksheets = [
        {
          name: "DETALHE1",
          fields: [
            { original: "Nº Sise", alias: "sise_key" },
            { original: "Aviso", alias: "notice" },
            { original: "Sistema", alias: "system" },
            { original: "Data Aviso", alias: "notice_date" },
            { original: "Tipo Perda", alias: "loss_type" },
            { original: "Competência", alias: "competence" },
          ],
        },
        {
          name: "DETALHE2",
          fields: [
            { original: "Data Ajuste", alias: "adjustment_date" },
            { original: "Cargo Ajuste", alias: "adjustment_role" },
            { original: "Dia da Semana (Ajuste)", alias: "adjustment_weekday" },
            { original: "AGREG(Alçada Ajuste)", alias: "adjustment_scope" },
            { original: "AGREG(Valor Ajuste)", alias: "adjustment_value" },
            { original: "AGREG(Valor Incorrido)", alias: "incurred_value" },
          ],
        },
        {
          name: "DETALHE3",
          fields: [
            { original: "Data Requisição", alias: "request_date" },
            { original: "Cargo Requisição", alias: "request_role" },
            { original: "Dia da Semana (Requisição)", alias: "request_weekday" },
            { original: "AGREG(Alçada Requisição)", alias: "request_scope" },
            { original: "AGREG(Valor Pagamento)", alias: "payment_value" },
          ],
        },
        {
          name: "DETALHE4",
          fields: [
            { original: "Data Aprovação", alias: "approval_date" },
            { original: "Cargo Aprovador", alias: "approver_role" },
            { original: "Dia da Semana (Aprovação)", alias: "approval_weekday" },
            { original: "AGREG(Alçada Aprovador)", alias: "approver_scope" },
            { original: "AGREG(Valor Pagamento - Aprovação)", alias: "approval_payment_value" },
          ],
        },
      ];

      const extractedData = {};

      console.log("Iniciando a extração de dados...");

      const promises = detailsWorksheets.map((worksheet) => {
        const ws = tableau.extensions.dashboardContent.dashboard.worksheets.find((w) => w.name === worksheet.name);
        if (!ws) {
          console.error(`Planilha '${worksheet.name}' não encontrada.`);
          return Promise.resolve();
        }

        return ws.getSummaryDataAsync().then((dataTable) => {
          console.log(`Planilha '${worksheet.name}' carregada.`);
          console.log("Colunas disponíveis na planilha:", dataTable.columns.map(col => col.fieldName));
          console.log("Dados disponíveis na planilha:", dataTable.data);

          worksheet.fields.forEach(({ original, alias }) => {
            const colIndex = dataTable.columns.findIndex((col) => col.fieldName === original);

            if (colIndex !== -1 && dataTable.data.length > 0) {
              const value = dataTable.data[0][colIndex]?._value || null;
              extractedData[alias] = value;
              console.log(`Extraído: ${alias} (${original}) = ${value}`);
            } else {
              extractedData[alias] = "Valor não encontrado";
              console.warn(`Campo '${original}' não encontrado na planilha '${worksheet.name}' ou sem dados.`);
            }
          });
        }).catch((error) => {
          console.error(`Erro ao obter dados da planilha '${worksheet.name}':`, error);
        });
      });

      return Promise.all(promises).then(() => {
        console.log("Extração finalizada com os dados:", extractedData);
        return extractedData;
      });
    },
    addWorksheetListeners() {
      const worksheetsToWatch = [
        { name: "DETALHE1", callback: this.fetchSiseKey },
        { name: "DETALHE2", callback: this.fetchSiseKey },
        { name: "DETALHE3", callback: this.fetchSiseKey },
        { name: "DETALHE4", callback: this.fetchSiseKey },
        { name: "Supp_analista_ajuste", callback: this.fetchAnalystsData },
        { name: "Supp_lob", callback: this.fetchLobData },
        { name: "Supp_area", callback: this.fetchAreaData },
      ];

      worksheetsToWatch.forEach(({ name, callback }) => {
        const worksheet = tableau.extensions.dashboardContent.dashboard.worksheets.find(w => w.name === name);
        if (!worksheet) {
          console.warn(`Planilha '${name}' não encontrada.`);
          return;
        }

        worksheet.addEventListener(tableau.TableauEventType.FilterChanged, () => {
          console.log(`Filtros alterados na planilha '${name}'. Atualizando...`);
          callback();
        });

        worksheet.addEventListener(tableau.TableauEventType.MarkSelectionChanged, () => {
          console.log(`Seleção alterada na planilha '${name}'. Atualizando...`);
          callback();
        });
      });

      console.log("Listeners de eventos adicionados às planilhas.");
    },
    updateAllData() {
      return new Promise((resolve, reject) => {
        if (!tableau.extensions.dashboardContent || !tableau.extensions.dashboardContent.dashboard) {
          console.error("Dashboard não está disponível. A API do Tableau pode não estar inicializada corretamente.");
          reject("Dashboard não inicializado.");
          return;
        }

        const dashboard = tableau.extensions.dashboardContent.dashboard;

        const dataSourcePromises = dashboard.worksheets.map((worksheet) =>
          worksheet.getDataSourcesAsync().then((dataSources) => {
            const refreshPromises = dataSources.map((dataSource) => {
              if (dataSource.refreshAsync) {
                console.log(`Atualizando fonte de dados: ${dataSource.name}`);
                return dataSource.refreshAsync();
              }
              console.warn(`A fonte de dados '${dataSource.name}' não suporta atualização.`);
              return Promise.resolve();
            });
            return Promise.all(refreshPromises);
          })
        );

        // Atualizar planilhas após atualizar fontes de dados
        Promise.all(dataSourcePromises)
          .then(() => {
            console.log("Fontes de dados atualizadas. Atualizando planilhas...");
            const worksheetPromises = dashboard.worksheets.map((worksheet) =>
              worksheet.getSummaryDataAsync().then(() => {
                console.log(`Dados da planilha '${worksheet.name}' atualizados.`);
              })
            );
            return Promise.all(worksheetPromises);
          })
          .then(() => {
            console.log("Atualização de dados e planilhas concluída.");
            resolve();
          })
          .catch((error) => {
            console.error("Erro ao atualizar os dados do Tableau:", error);
            reject(error);
          });
      });
    },
    addWorksheetListeners() {
      tableau.extensions.dashboardContent.dashboard.worksheets.forEach((worksheet) => {
        worksheet.addEventListener(tableau.TableauEventType.FilterChanged, () => {
          console.log(`Filtro alterado em '${worksheet.name}'. Nenhuma atualização será feita.`);
        });

        worksheet.addEventListener(tableau.TableauEventType.MarkSelectionChanged, () => {
          console.log(`Seleção alterada em '${worksheet.name}'. Nenhuma atualização será feita.`);
        });
      });

      console.log("Listeners adicionados aos worksheets.");
    }

  },
  mounted() {
    // tableau.extensions.initializeAsync().then(() => {
    //   console.log("API do Tableau inicializada com sucesso");
    //   this.fetchAnalystsData();
    //   this.fetchLobData();
    //   this.fetchAreaData();
    //   this.fetchSiseKey();
    //   this.addMarkSelectionEventHandler();

    //   this.fetchDetailsData();
    //   this.addWorksheetListeners();
    // }).catch((error) => {
    //   console.error("Erro ao inicializar a API do Tableau:", error);
    // });

    tableau.extensions.initializeAsync()
      .then(() => {
        console.log("API do Tableau inicializada com sucesso.");
        this.initializeDashboard();
      })
      .catch((error) => {
        console.error("Erro ao inicializar a API do Tableau:", error);
      });

    // Ajustes de estilo
    const inputSlotElements = document.querySelectorAll('.v-input__slot');
    inputSlotElements.forEach(element => {
      element.style.marginBottom = '0px';
    });

    const messagesElements = document.querySelectorAll('.v-messages');
    messagesElements.forEach(element => {
      element.style.minHeight = '0px';
    });

    const fieldOverlayElements = document.querySelectorAll('.v-field__overlay');
    fieldOverlayElements.forEach(element => {
      element.style.color = '#ffffff00';
    });
  }
};
</script>


<style scoped>
.col-2 {
  flex: 1;
  min-width: 0;
}

.margens {
  padding: 20px;
}

.small-text {
  font-size: 12px !important;
}
</style>
